import Bus from 'framebus'
import { getUrls, PaymentCryptoResult } from 'payment-core'
import { threeDSecureEvents } from '@/constants'
import { HostedFieldsOptionModel } from '../models/HostedFieldsOptionModel'

export class ThreeDSecure {

    bus: Bus
    isTest: boolean
    authToken: string
    isIframeLoaded: boolean = false
    paymentResult: PaymentCryptoResult = null
    paymentReply: (data: any) => void
    webSocket: WebSocket = null

    form = document.querySelector('#output_form') as HTMLFormElement
    iframe = document.querySelector('#output_frame') as HTMLIFrameElement
    hiddenInputs = {
        paReq: document.querySelector('[name="PaReq"]') as HTMLInputElement,
        md: document.querySelector('[name="MD"]') as HTMLInputElement,
        termUrl: document.querySelector('[name="TermUrl"]') as HTMLInputElement
    }

    get urls() {
        return getUrls(this.isTest)
    }

    constructor(instanceId: string) {

        this.bus = new Bus({ channel: instanceId })
        this.iframe.addEventListener('load', this.onIframeLoad.bind(this))

        this.bus.emit(threeDSecureEvents.FRAME_READY, {}, this.initialize.bind(this))

        this.bus.on(threeDSecureEvents.SET_DATA, (data: any, reply) => {
            this.paymentReply = reply
            this.setData(data)
        })
    }

    initialize(options: HostedFieldsOptionModel) {
        this.isTest = options.isTest
        this.authToken = options.authToken
    }

    onIframeLoad() {
        if (this.paymentResult && this.paymentResult.secure3D.action === this.iframe.src) {
            this.bus.emit(threeDSecureEvents.SHOW)
        }
    }

    setData(paymentResult: PaymentCryptoResult) {
        const { action, md, paReq } = paymentResult.secure3D
        this.hiddenInputs.paReq.value = paReq
        this.hiddenInputs.md.value = md
        this.hiddenInputs.termUrl.value = `${this.urls.apiUrl}/payments/cards/confirmPayment?Access=${this.authToken}`
        this.form.action = action
        this.iframe.src = action
        this.paymentResult = paymentResult
        this.addWsListenerTo3dSecureResult()

        setTimeout(() => this.form.submit())
    }

    hide() {
        console.log('hide')
        this.bus.emit(threeDSecureEvents.HIDE)
        this.clear()
    }

    clear() {
        this.hiddenInputs.paReq.value = ''
        this.hiddenInputs.md.value = ''
        this.hiddenInputs.termUrl.value = ''
        this.form.action = ''
        this.iframe.src = '#'
        this.paymentResult = null
        if (this.webSocket) {
            this.webSocket.close()
        }
        this.webSocket = null
    }

    handle3dSecureResult(query: { result: string }) {
        console.log('query', query)
        if (query && query.result) {
            if (query.result === 'ok') {
                this.paymentReply([null, query.result])
            } else if (query.result === 'error') {
                this.paymentReply([{}, null])
            }
        }
        this.hide()
    }

    addWsListenerTo3dSecureResult() {
        let tm
        let pingInterval
        const ws = new WebSocket(`${ this.urls.wsUrl }/${ this.paymentResult.id }`)

        function ping() {
            ws.send('__ping__')
            tm = setTimeout(() => {
                log('websocket channel closed')
            }, 5000)
        }

        function pong() {
            clearTimeout(tm)
        }

        ws.onopen = () => {
            log('websocket channel connected')
            pingInterval = setInterval(ping, 30000)
        }

        ws.onmessage = evt => {
            const msg = evt.data
            if (msg === '__pong__') {
                pong()
                return
            }
            const message: WebSocketResult = JSON.parse(evt.data)
            if (message.result) {
                this.handle3dSecureResult({ result: message.result.result })
            }
        }

        ws.onclose = e => {
            clearInterval(pingInterval)
            log(e.reason)
        }

        ws.onerror = err => {
            clearInterval(pingInterval)
            log(
                'Socket encountered error: ',
                err,
                'Closing socket'
            )
        }

        this.webSocket = ws
    }
}

interface WebSocketResult {
    id: string,
    result: {
        errorCode: number
        errorMessage: string,
        reference: string
        result: 'ok' | 'error'
    }
}

const log = console.log